			}

			&:nth-of-type(3) {
				border-top: 1px solid var(--light-gray);
				grid-row: 2/4;
				border-right: 1px solid var(--light-gray);
			}

			&:nth-of-type(4) {
				border-top: 1px solid var(--light-gray);
				padding: 70px;

				p {
					max-width: 600px;
				}
			}

			&:nth-of-type(5) {

				.arrow-button {
					padding: 30px 60px;
					text-align: right;
					display: flex;
					justify-content: flex-end;
					border-top: 1px solid var(--light-gray);
				}

			}
		}
	}

	.search-results {
		border-bottom: 1px solid var(--light-gray);

		.wrapper {
			padding: 20px 20px;
			max-width: 1252px;
		}
	}

	.checkout__heading {
		padding-bottom: 15px;
		width: 100%;
		display: block;
		border-bottom: 1px solid var(--light-gray);
		margin-top: 40px;
		margin-bottom: 30px;
	}

	.checkout {
		display: grid;
		grid-template-columns: minmax(55%, 1fr) minmax(350px, 520px);

		fieldset {
			display: grid;
			grid-template-columns: 1fr 1fr;
			column-gap: 55px;
		}

		select {
			padding-bottom: 10px;
			border-bottom: 1px solid var(--black);
		}
	}

	.checkout-main {
		padding: 0 60px;
	}

	.checkout__heading,
	.checkout-main fieldset,
	.checkout-main label,
	.checkout-main select {
		max-width: 620px;
		width: 100%;
	}

	.checkout__heading,
	.checkout__title,
	.checkout__section,
	.checkout__section label {
		display: block;
		margin-bottom: 25px;
	}

	.checkout__table {
		width: 100%;

		tr {
			display: grid;
			grid-template-columns: 30% 30% 40%;
			align-items: center;
			column-gap: 10px;
		}

		td,
		th {
			padding: 0 5px;
			word-break: break-all;
		}

		tbody {
			border: 1px solid var(--light-gray);
			border-bottom: 0;

			tr {
				padding: 20px 0;
				border-bottom: 1px solid var(--light-gray);
			}
		}

		tfoot {
			padding-top: 20px;

			tr {
				display: block;
			}

			display: flex;
			justify-content: flex-end;
		}

		li {
			display: flex;

			p:first-child {
				margin-right: 10px;
			}
		}
	}

	.cart-pre-summary {
		padding-top: 20px;
		padding-bottom: 50px;

		li {
			margin-bottom: 20px;
		}
	}

	.checkout-summary {
		border-left: 1px solid var(--light-gray);

		.cart-main {
			height: 100%;
		}
	}

	.checkout-navigation {
		border-top: 1px solid var(--light-gray);
		display: grid;
		grid-template-columns: minmax(55%, 1fr) minmax(350px, 520px);


		.back-button,
		.cart__checkout-btn {
			padding: 25px;
		}

		.back-button span {
			display: flex;
			align-items: center;
		}

		.back-button svg {
			margin-right: 15px;
		}

		.cart__checkout-btn {
			text-align: center;
		}
	}

	.checkout-summary .cart-main {
		padding: 55px 60px 24px 60px;
	}
}